<template>
     <div class="content">
       <!-- 轮播开始 -->
         <div class="swiper-container ">
        <swiper :options="swiperOption" ref="mySwiper" class="banner" v-if="banners && banners.length!= 0 ">
      <swiper-slide  v-for="v in banners" :key='v.image'>
        <div class="simg" :style="{backgroundImage:`url(${v.image})`}"></div>
        <!-- <img :src='v.image' alt=""> -->
        </swiper-slide>
      <!-- <swiper-slide><img src="../assets/two.jpg" alt=""></swiper-slide>
      <swiper-slide><img src="../assets/three.jpg" alt=""></swiper-slide> -->
      <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
  </div>
         <!-- 轮播结束 -->
         <div id='nav' >
           <ul>
             <li>
               <router-link :to="{name:'Home'}">首页</router-link>
             </li>
              <li>
              <router-link :to="{name:'Home'}">首页</router-link>
             </li>
              <li>
             <router-link :to="{name:'Home'}">首页</router-link>
             </li>
            
           </ul>
         </div>
          <article-item v-for='v in lists' v-bind='v' :key='v._id'></article-item>
          <!-- 加载 -->
          <div class="loadding" ref="loadding">
           <span v-show="loaddingStage"> 加载中....</span>
          </div>

     </div>

</template>

<script>

import { swiper, swiperSlide } from "vue-awesome-swiper";
import "vue-awesome-swiper/node_modules/swiper/dist/css/swiper.min.css";
import ArticleItem from '../components/ArticleItem.vue';

export default {
    name:'Home',
    components: {
    swiper,
    swiperSlide,
    ArticleItem
       
    // ajax
  },
  data(){
     return {
       banners:[],
       lists:[],
       page:1,
       totalPage:0,
       loaddingStage:false,
     //swiper 初始化
      swiperOption: {
        loop: true,
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },
        // 显示分页
        pagination: {
          el: ".swiper-pagination",
          clickable: true //允许分页点击跳转
        },
      
      }
    };


  },
  mounted(){
       var that =this
     this.ajax({
       url:'https://gank.io/api/v2/banners',
       success:function(data){
        //  console.log(data);
          var data=JSON.parse( data)
           if(data.status==100){

              that.banners=data.data
            
            }else{
              alert('获取失败！')
            }
       }
     })
         this.loaddingStage=true
       this.ajax({
            url:'https://gank.io/api/v2/data/category/GanHuo/type/All/page/1/count/10',
            success:function(data){
                var data=JSON.parse( data)
                    console.log(data);
                if(data.status==100){
                     that.lists=data.data
                     that.loaddingStage=false
                     that.totalPage=data.page_count
                    }else{
                     alert('获取失败！')
                  }
                }
             })
    var loadding=this.$refs.loadding
        window.onscroll=function(){
          
           if( that.loaddingStage==false && loadding.getBoundingClientRect().bottom<window.innerHeight && that.totalPage >that.page ){
              that.loaddingStage=true
              that.page+=1
              that.ajax({
            url:"https://gank.io/api/v2/data/category/GanHuo/type/All/page/" + that.page + "/count/10",
            success:function(data){
                var data=JSON.parse( data)
                    console.log(data);
                if(data.status==100){
                     that.lists=that.lists.concat(data.data)
                     that.loaddingStage=false
                    }else{
                     alert('获取失败！')
                  }
                }
             })
           }
         
        }
  //      var xhr=new XMLHttpRequest();
  //      xhr.open('get','https://gank.io/api/v2/banners');
  //      xhr.send()
  //      xhr.onreadystatechange = function(){
  //     // 当xhr状态 == 4 响应内容解析完成，可使用
  //     if(xhr.readyState == 4){
  //         // http响应状态 
  //         if(xhr.status == 200){
  //            var data=JSON.parse( xhr.response)
  //             console.log(data);
  //             // 获取响应结果
  //           if(data.status==100){
            
  //             that.banners=data.data
  //           }else{
  //             alert('获取失败！')
  //           }
  //         }
  //     }
// }
  }

 }
 
</script>
