.banner{
 
    height:2.6rem;
   
  /* padding:0 0.2rem; */
  box-sizing: border-box;
}
.simg{
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.swiper-pagination-bullet{
  width: 0.16rem;
  height: 0.04rem;
  border-radius: 0;
    transition: width 1s inherit;
    background: rgba(255, 255, 255, .5);
}
.swiper-pagination-bullet-active{
  width: 0.32rem;
  background: #fff;

}
#nav{
margin-top: 0.2rem;
}
#nav ul{
  width: 100%;
  overflow: hidden;

}
#nav ul li{
  float: left;
  /* margin-right: 0.2rem; */
  padding:.2rem .5rem .2rem .5rem;
  box-sizing: border-box;
  font-size: 0.12rem;
}
#nav ul li a{
 color: #B1B1C1;
}
#nav ul li:active{
  background: #2c63ff;


} 
