<template>
     <div class="article">
         <span class="label">{{articles.type}}</span>
              <h3>{{articles.title}}</h3>
              <div v-html='articles.content'  class="markdown-body">

              </div>
            
    </div>
</template>

<script>
import marked from 'marked'
export default {
    data(){
      return {
        articles:{}
      }
    },
     mounted(){
       var that =this

       this.ajax({
            url:' https://gank.io/api/v2/post/'+that.$route.params.id,
            success:function(data){
                var data=JSON.parse( data)
                    console.log(data);
                if(data.status==100){
                     that.articles=data.data
                     that.articles.content=marked(data.data.markdown)
                    
                    }else{
                     alert('获取失败！')
                  }
                }
             })
     }
   
}
</script>
<style >
@import url(../assets/css/markdown.css);
.article{
    width: 100%;
    padding: 0.3rem;
    box-sizing: border-box;
    overflow: hidden;
    color: #fff;
    margin: auto;
    background: #1D1F20;
    margin-top: 0.16rem;
    margin-bottom: 0.16rem;
}
.label{
  background: #2c63ff;
  padding: 0.05rem;
  font-size: 0.2rem;
 
  
}
.article h3{
  font-size: 24px;
  margin: 0.3rem 0;
}
.article .desc{
  color: #B1B1C1;
  margin-top: 0.4rem;
}
.copy{
  margin-top: 0.2rem;
color: #fff;
font-size: 0.14rem;
}
.copy a{
  color: #fff;
}
.copy span{
  margin-left: 0.1rem;
  color: #B1B1C1;
}
</style>
