import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import List from '../views/List.vue'
import Show from '../views/Show.vue'
Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/list/:cate_id',
        name: 'List',
        component: List
    }, {
        path: '/show/:id',
        name: 'Show',
        component: Show

    }
]

const router = new VueRouter({
    routes
})

export default router