<template>
    <div>
      <ul class="list">
        <li>
            <router-link :to="'/Show/'+_id">
                <div class="lefting">
                <img :src="images[0]" alt="">
              </div>
             <div class="right">
                <span class="label">{{type}}</span>
              <h3 class="hidden1">{{title}}</h3>
              <div class="desc hidden2">
                {{desc}}
              </div>
              <div class="copy">
                <a href="">{{author}}</a>- <a href="">{{type}}</a><span class="time">{{createdAt|date}}</span>
              </div>
            </div>
            </router-link>
        </li>
      </ul>
    </div>
</template>
<script>
export default {
  props:[
      "_id",
     'createdAt',
     'title',
     'author',
     'type',
     'images',
     'desc',
     'views',
  ],
  filters:{
   "date":function(val,format='yyyy-MM-dd'){
    var date = new Date(val);
    if (/(y+)/.test(format)) {
        format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
        'M+': date.getMonth() + 1,
        'd+': date.getDate(),
        'h+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds()
    };
    for(let k in o){
        if (new RegExp(`(${k})`).test(format)) {
            let str = o[k] + '';
            format = format.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length));
        }
    }
    return format;
  }
  } 
     
  
   
    

}
</script>
<style >


.list{
  width: 100%;
  /* margin-top: 0.2rem; */
  /* padding: 0.2rem 0; */

}
.list li{
  overflow: hidden;
  background: #1D1F20;
  padding: 0.3rem;
  box-sizing: border-box;
  margin-bottom: 0.4rem;

}
.list li a{
  color: #fff;
}
.lefting{
  width: 2.2rem;
  height: 2.2rem;
  overflow: hidden;
  float: left;
}
.lefting img{
  width: 100%;
  height: 100%;
}
.right{
  width: calc(100% - 2.6rem);
  float: right;
  color: #fff;
  overflow: hidden;
}
.label{
  background: #2c63ff;
  display: inline-block;
   /* vertical-align: middle; */
  
}
.right h3{
  /* display: inline-block; */
  margin-left: 0.2rem;
  /* vertical-align: middle; */
}
.right .desc{
  color: #B1B1C1;
  margin-top: 0.2rem;
}
.copy{
  margin-top: 0.2rem;
color: #fff;
font-size: 0.14rem;
}
.copy a{
  color: #fff;
}
.copy span{
  margin-left: 0.1rem;
  color: #B1B1C1;
}
.time{
  float: right;
}
</style>